import React, { useContext, useEffect } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faUser } from '@fortawesome/free-solid-svg-icons';
import cross from '../assets/img/cross.svg';
import { EmptyText } from '../styles/styles';
import { Trigger } from '../App';

const EmptyRouter = () => {
  const {
    localUser,
      localPath,
    setLocalPath,
    notFound,
    setLocalIcon,
    setLocalText,
    localIcon,
    localText,
  } = useContext(Trigger);
  const repos = JSON.parse(sessionStorage.getItem('repos'));
  useEffect(() => {
    if (localPath === process.env.REACT_APP_USER_ROUTE && repos.length === 0) {
      setLocalIcon(<img src={cross} alt="empty repos" />);
      setLocalText('Repository list is empty');
      return;
    }
    if (localPath === process.env.REACT_APP_USER_ROUTE) {
      setLocalIcon('');
      setLocalText('');
    }
    if (localUser) {
      if (localUser.login) {
        setLocalPath(`${process.env.REACT_APP_USER_ROUTE}`);
        return;
      }
      if (localUser === notFound) {
        setLocalIcon(<FontAwesomeIcon icon={faUser} />);
        setLocalText('User not found');
      }
    }
  }, [localUser]);
  return (
    <EmptyText>
      <div>{localIcon}</div>
      <div>{localText}</div>
    </EmptyText>
  );
};

export default EmptyRouter;
