import React, { useContext, useEffect } from 'react';
import { Trigger } from '../App';
import EmptyRouter from '../components/EmptyRouter';
import { EmptyContainer } from '../styles/styles';

function Empty() {
  const { setLocalUser, notFound } = useContext(Trigger);
  const getUser = JSON.parse(sessionStorage.getItem('user'));
  useEffect(() => {
    if (getUser.message === notFound) {
      setLocalUser(notFound);
      return;
    }
    setLocalUser(getUser);
  }, []);
  return (
    <EmptyContainer>
      <EmptyRouter />
    </EmptyContainer>
  );
}

export default Empty;
