import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faGithub } from '@fortawesome/free-brands-svg-icons';
import { faSearch } from '@fortawesome/free-solid-svg-icons';
import { useContext } from 'react';
import { Redirect } from 'react-router';
import * as userApi from '../api/userApi';
import { Trigger } from '../App';
import { NavbarForm, NavbarGithub, NavbarHeader } from '../styles/styles';

const Navbar = () => {
  const {
    notFound,
    setLocalSpinner,
    localPath,
    setLocalPath,
    searchInput,
    setSearchInput,
  } = useContext(Trigger);
  return (
    <NavbarHeader>
      <NavbarGithub>
        <FontAwesomeIcon icon={faGithub} />
      </NavbarGithub>
      <NavbarForm>
        <label>
          <FontAwesomeIcon icon={faSearch} />
          <input
            autoComplete="off"
            onChange={(e) => {
              setSearchInput(e.currentTarget.value);
            }}
            id="search"
            type="text"
            value={searchInput}
            onKeyDown={(e) => {
              if (e.key === 'Enter') {
                debugger
                setLocalSpinner(true);
                const user = userApi.getUser(`${searchInput}`);
                const repos = userApi.getRepos(`${searchInput}`);
                Promise.all([user, repos]).then((result) => {
                  sessionStorage.setItem('user', JSON.stringify(result[0]));
                  sessionStorage.setItem('repos', JSON.stringify(result[1]));
                  setLocalSpinner(false);
                  setLocalPath(`${process.env.REACT_APP_USER_ROUTE}`);
                  if (result[0].message === notFound) setLocalPath(`${process.env.REACT_APP_HOME_ROUTE}`);
                });
                e.preventDefault();
                setSearchInput('');
              }
            }}
          />
        </label>
      </NavbarForm>
      <Redirect to={{ pathname: `${localPath}` }} />
    </NavbarHeader>
  );
};

export default Navbar;
