import React, { useContext, useEffect } from 'react';
import ReactPaginate from 'react-paginate';
import {
  faChevronLeft,
  faChevronRight,
} from '@fortawesome/free-solid-svg-icons';
import PaginateItem from './PaginateItem';
import {
  UserItem,
  UserItems,
  UserPagination,
  UserRepos,
  UserReposTitle,
} from '../styles/styles';
import { Trigger } from '../App';

function Repositories() {
  let repos = JSON.parse(sessionStorage.getItem('repos')) || '';
  const {
    notFound,
    localRepoPage,
    localRepoSize,
    setLocalStartRepos,
    setLocalEndRepos,
    setLocalAllPage,
    startRepos,
    endRepos,
    allPage,
    setLocalRepoPage,
  } = useContext(Trigger);
  useEffect(() => {
    const endReposBuffer=localRepoPage * localRepoSize - 1
    setLocalStartRepos((localRepoPage - 1) * localRepoSize);
    setLocalEndRepos(
        endReposBuffer <= repos.length
        ? endReposBuffer
        : repos.length - 1
    );
    setLocalAllPage(Math.abs(repos.length / localRepoSize));
  }, [localRepoPage, localRepoSize]);
  let display = { display: 'block' };
  if (repos.length === 0) display = { display: 'none' };
  if (repos.message === notFound) repos = [];
  return (
    <UserRepos style={display}>
      <UserReposTitle>Repositories ({`${repos.length}`})</UserReposTitle>
      <UserItems>
        {repos.map((el, ind) => {
          const { name, svn_url, description } = el;
          if (ind >= startRepos && ind <= endRepos)
            return (
              <UserItem key={name}>
                <a href={svn_url} target="_blank" rel="noreferrer">
                  {name}
                </a>
                <p>{description}</p>
              </UserItem>
            );
          return undefined;
        })}
      </UserItems>
      <UserPagination>
        <p>{`${startRepos + 1}-${endRepos + 1} of ${repos.length} items`}</p>
        <ReactPaginate
          previousLabel={<PaginateItem icon={faChevronLeft} />}
          nextLabel={<PaginateItem icon={faChevronRight} />}
          pageCount={allPage}
          onPageChange={(page) => {
            setLocalRepoPage(page.selected + 1);
          }}
          initialPage={localRepoPage - 1}
          marginPagesDisplayed={1}
          pageRangeDisplayed={2}
          breakLabel="..."
          breakClassName="break-me"
          containerClassName="pagination"
          activeClassName="active"
        />
      </UserPagination>
    </UserRepos>
  );
}

export default Repositories;
