import React, { useEffect, useState } from 'react';
import { faSearch, faSpinner } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import Navbar from './components/Navbar';
import AppRoutes from './routers/AppRoutes';
import { AppContainer, AppSpinner } from './styles/styles';

export const Trigger = React.createContext({});

function App() {
  const [searchInput, setSearchInput] = useState('');
  const [localPath, setLocalPath] = useState(`${process.env.REACT_APP_HOME_ROUTE}`);
  const [localSpinner, setLocalSpinner] = useState(false);
  const [localUser, setLocalUser] = useState('');
  const [notFound] = useState('Not Found');
  const [localRepoPage, setLocalRepoPage] = useState(1);
  const [localRepoSize, setLocalRepoSize] = useState(4);
  const [startRepos, setLocalStartRepos] = useState(0);
  const [endRepos, setLocalEndRepos] = useState(0);
  const [allPage, setLocalAllPage] = useState(0);
  const [localIcon, setLocalIcon] = useState(
    <FontAwesomeIcon icon={faSearch} />
  );
  const [localText, setLocalText] = useState(
    'Start with searching a GitHub user'
  );
  if (!JSON.parse(sessionStorage.getItem('repos')) || JSON.parse(!sessionStorage.getItem('user'))) {
      console.log("2")
      sessionStorage.setItem('user', JSON.stringify({}));
    sessionStorage.setItem('repos', JSON.stringify([]));
  }
  useEffect(() => {
    window.addEventListener('unload', () => {
      sessionStorage.removeItem('user');
      sessionStorage.removeItem('repos');
    });
  }, []);
  return (
    <AppContainer>
      <Trigger.Provider
        value={{
          searchInput,
          setSearchInput,
          localRepoPage,
          localRepoSize,
          startRepos,
          endRepos,
          allPage,
          setLocalRepoPage,
          setLocalStartRepos,
          setLocalEndRepos,
          setLocalAllPage,
          setLocalRepoSize,
          setLocalSpinner,
          localPath,
          setLocalPath,
          localUser,
          setLocalUser,
          notFound,
          localIcon,
          setLocalIcon,
          localText,
          setLocalText,
        }}>
        <Navbar />
        {localSpinner ? (
          <AppSpinner>
            <FontAwesomeIcon icon={faSpinner} spin />
          </AppSpinner>
        ) : (
          <AppRoutes />
        )}
      </Trigger.Provider>
    </AppContainer>
  );
}

export default App;
